/**
 *    .
 * @param {string | Date} data -       Date.
 * @returns {string}     ( date.day).
 */

export function parseDate (data) {
	let date;

	if (typeof data === 'string') {
		date = new Date(data);
	} else if (data instanceof Date) {
		date = data;
	} else {
		throw new Error('Invalid data type. Expected string or Date object.');
	}

	const year = date.getFullYear();
	const month = String(date.getMonth() + 1).padStart(2, "0");
	const day = String(date.getDate()).padStart(2, "0");

	const hours = String(date.getHours()).padStart(2, "0");
	const minutes = String(date.getMinutes()).padStart(2, "0");
	const seconds = String(date.getSeconds()).padStart(2, "0");

	const result = {
		year,
		month,
		day,
		hours,
		minutes,
		seconds
	}
	
  	return result;
}

/**
 *       .
 * @param {string | Date} data -       Date.
 * @param {string} format -   (, "Y-M-D h:m:s").
 * @returns {string}     .
*/

export function formatDate (data, format) {
	const date = parseDate(data)

	let formattedDate = format
    .replace("Y", date.year)
    .replace("y", String(date.year).slice(-2))
    .replace("M", date.month)
    .replace("D", date.day)
    .replace("h", date.hours)
    .replace("m", date.minutes)
    .replace("s", date.seconds);

  	return formattedDate;
}


/**
 *  .
 * @param {string} string -        Date.
 * @returns {string}   .
 */

export function formatPhoneNumber(phoneNumber) {
	const phone = String(phoneNumber)
	const countryCode = phone.slice(0, 1); //    -  
	const areaCode = phone.slice(1, 4); //     -  
	const firstPart = phone.slice(4, 7); //    
	const secondPart = phone.slice(7, 9); //    
	const thirdPart = phone.slice(9); //     
  
	return `${countryCode} (${areaCode}) ${firstPart}-${secondPart}-${thirdPart}`;
}